export default `
<script>
{
    //pop() 方法用于删除数组的最后一个元素并返回删除的元素
    let fruits = ["Banana", "Orange", "Apple", "Mango"];
    write(fruits.pop())
    write(fruits)
}
write('<br>')
{
    //shift() 方法用于删除数组的第一个元素并返回删除的元素
    let fruits = ["Banana", "Orange", "Apple", "Mango"];
    write(fruits.shift())
    write(fruits)
}
write('<br>')
{
    //push() 方法可向数组的末尾添加一个或多个元素，并返回新的长度
    let fruits = ["Banana", "Orange", "Apple", "Mango"];
    write(fruits.push("Lemon"))
    write(fruits)
}
write('<br>')
{
    //unshift() 方法可向数组的开头添加一个或更多元素，并返回新的长度
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    write(fruits.unshift("Lemon","Pineapple"))
    write(fruits)
}
write('<br>')
</script>
`



